import quickBtn from '@/components/mbbo/quickBtn/quickBtn.vue';
import navigation from "@/components/mbbo/navigation/navigation.vue";
import { mapMutations, mapGetters } from 'vuex';
export default {
	data() {
		return {
			list: [],
			num: 0,
			biao: this.Static+'my/biao.png',
			ding: this.Static+'my/ding.png',
			up: this.Static+'my/up-arrow.png',
			phone: this.Static+'my/phone.png',
			id:'',
			thid:[],
			Image: this.Static+'my/Image123.png',
		}
	},
	components: {
		quickBtn,
		navigation,
	},
	computed: {
		i18n () {
		  return this.$t('Howtoshow')  
		},
		...mapGetters(['GET_HOME', 'QUICKNAVCO'])
	},
	filters: {
		formatDate: function(value) {
			let date = new Date(value);
			let y = date.getFullYear();
			let MM = date.getMonth() + 1;
			MM = MM < 10 ? ('0' + MM) : MM;
			let d = date.getDate();
			d = d < 10 ? ('0' + d) : d;
			let h = date.getHours();
			h = h < 10 ? ('0' + h) : h;
			let m = date.getMinutes();
			m = m < 10 ? ('0' + m) : m;
			let s = date.getSeconds();
			s = s < 10 ? ('0' + s) : s;
			return y + '-' + MM + '-' + d +' ' + h + ':' + m + ':' + s; 
		}
	},
	onLoad:function(options){
		this.id = options.id
		console.log(this.id)
		uni.setNavigationBarTitle({
			title: this.i18n.product8
		});
		this.details();
	},
	created(){
		// this.details();
	},
	methods:{
		playphone(e){
			console.log(e,123)
			uni.makePhoneCall({
				phoneNumber: e //仅为示例
			});
		},
		details(){
			console.log(this.id)
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				// let params = {}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/order/selectOrderInfo?orderId=' + this.id, //接口地址。
					// data: params,
					method: 'GET',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						this.list = response.data.content
						// this.$store.commit('setSupply', this.gong);
						// this.lists = response.data.content.instKeys
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
	}
	
}
